.statistics {
  display: flex;
  flex-direction: column;
  width: 375px;
  height: 100%;
  background-color: rgb(243, 243, 243);
  border-radius: 5px;
  box-shadow: 0px 0px 1px 1px gray;
  margin-top: 10px;
  margin-bottom: 10px;
}

.title {
  display: flex;
  width: 100%;
  height: 75px;
  justify-content: center;
  align-items: center;
  margin: 0;
  text-transform: uppercase;
  font-size: 18px;
  border-radius: 5px;
}

.stat__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.stat__list:first-child {
  border-radius: 0px 0px 0px 5px;
}

.stat__list:last-child {
  border-radius: 0px 0px 5px 0px;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 75px;
  height: 75px;
}

:first-child .label {
  color: white;
  font-size: 14px;
  margin-bottom: 2px;
}

.percentage {
  color: white;
  font-size: 18px;
}

.hidden {
  display: none;
}
